/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useState, useTransition, useRef, ChangeEvent } from "react";
import Image from "next/image";
import { ProfileData, profileSchema } from "../schema";
import { handleUpdateProfile } from "@/lib/action/profile-action";

interface ProfileFormProps {
  userId: string;
  initialData?: {
    name?: string;
    email?: string;
    bio?: string;
    phone?: string;
    image?: string;
  };
}

export default function ProfileForm({ userId, initialData }: ProfileFormProps) {
  const [pending, startTransition] = useTransition();
  const [error, setError] = useState<string>("");
  const [success, setSuccess] = useState<string>("");
  const [imagePreview, setImagePreview] = useState<string>(
    initialData?.image || "/images/default-avatar.png"
  );
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<ProfileData>({
    resolver: zodResolver(profileSchema),
    mode: "onSubmit",
    defaultValues: {
      name: initialData?.name || "",
      email: initialData?.email || "",
      bio: initialData?.bio || "",
      phone: initialData?.phone || "",
    },
  });

  const handleImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    setError("");
    const file = e.target.files?.[0];
    if (!file) return;

    const validTypes = ["image/jpeg", "image/jpg", "image/png", "image/gif", "image/webp"];
    if (!validTypes.includes(file.type)) {
      setError("Please select a valid image file (JPG, PNG, GIF, or WebP)");
      return;
    }

    if (file.size > 5 * 1024 * 1024) {
      setError("Image size must be less than 5MB");
      return;
    }

    const reader = new FileReader();
    reader.onloadend = () => {
      setImagePreview(reader.result as string);
    };
    reader.readAsDataURL(file);
    setSelectedFile(file);
  };

  const handleRemoveImage = () => {
    setImagePreview(initialData?.image || "/images/default-avatar.png");
    setSelectedFile(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  const onSubmit = async (data: ProfileData) => {
    setError("");
    setSuccess("");
    try {
      const formData = new FormData();
      formData.append("name", data.name);
      formData.append("email", data.email);
      
      if (data.bio) {
        formData.append("bio", data.bio);
      }
      
      if (data.phone) {
        formData.append("phone", data.phone);
      }

      if (selectedFile) {
        formData.append("image", selectedFile);
      }

      const res = await handleUpdateProfile(userId, formData);
      if (!res.success) {
        throw new Error(res.message || "Profile update failed");
      }

      setSuccess(res.message || "Profile updated successfully!");
      
      if (res.data?.image) {
        setImagePreview(res.data.image);
      }

      setSelectedFile(null);
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
    } catch (err: any) {
      setError(err?.message || "Profile update failed");
    }
  };

  return (
    <form
      onSubmit={handleSubmit(onSubmit)}
      className="space-y-6 p-6 bg-white rounded-lg shadow-md w-full max-w-2xl mx-auto"
    >
      <h2 className="text-2xl font-semibold text-gray-800 text-center">
        üå∏ Update Your Profile üå∏
      </h2>

      {error && (
        <div className="p-3 bg-red-50 border border-red-200 rounded-md">
          <p className="text-sm text-red-600">{error}</p>
        </div>
      )}

      {success && (
        <div className="p-3 bg-green-50 border border-green-200 rounded-md">
          <p className="text-sm text-green-600">{success}</p>
        </div>
      )}

      <div className="flex flex-col items-center space-y-4">
        <div className="relative group">
          <div className="relative w-32 h-32 rounded-full overflow-hidden border-4 border-pink-200 shadow-lg">
            <Image
              src={imagePreview}
              alt="Profile Picture"
              fill
              className="object-cover"
              priority
            />
          </div>
          <div
            onClick={() => fileInputRef.current?.click()}
            className="absolute inset-0 bg-black bg-opacity-50 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-200 flex items-center justify-center cursor-pointer"
          >
            <span className="text-white text-xs font-medium">Change Photo</span>
          </div>
        </div>

        <input
          ref={fileInputRef}
          type="file"
          accept="image/jpeg,image/jpg,image/png,image/gif,image/webp"
          onChange={handleImageChange}
          className="hidden"
        />

        <div className="flex gap-2">
          <button
            type="button"
            onClick={() => fileInputRef.current?.click()}
            className="px-4 py-2 text-xs rounded-md bg-pink-100 text-pink-600 font-medium hover:bg-pink-200 transition-colors"
          >
            üìÅ Choose Photo
          </button>
          <button
            type="button"
            onClick={handleRemoveImage}
            className="px-4 py-2 text-xs rounded-md bg-gray-100 text-gray-600 font-medium hover:bg-gray-200 transition-colors"
            disabled={!selectedFile}
          >
            üóëÔ∏è Remove
          </button>
        </div>
        <p className="text-xs text-gray-500 text-center">
          JPG, PNG, GIF, or WebP (Max 5MB)
        </p>
      </div>

      <div className="space-y-1">
        <label htmlFor="name" className="text-sm font-medium text-gray-700">
          Full Name <span className="text-red-500">*</span>
        </label>
        <input
          id="name"
          type="text"
          placeholder="Enter your full name"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("name")}
        />
        {errors.name && (
          <p className="text-xs text-red-600">{errors.name.message}</p>
        )}
      </div>

      <div className="space-y-1">
        <label htmlFor="email" className="text-sm font-medium text-gray-700">
          Email <span className="text-red-500">*</span>
        </label>
        <input
          id="email"
          type="email"
          placeholder="your.email@example.com"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("email")}
        />
        {errors.email && (
          <p className="text-xs text-red-600">{errors.email.message}</p>
        )}
      </div>

      <div className="space-y-1">
        <label htmlFor="phone" className="text-sm font-medium text-gray-700">
          Phone Number (Optional)
        </label>
        <input
          id="phone"
          type="tel"
          placeholder="9812345678"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("phone")}
        />
        {errors.phone && (
          <p className="text-xs text-red-600">{errors.phone.message}</p>
        )}
      </div>

      <div className="space-y-1">
        <label htmlFor="bio" className="text-sm font-medium text-gray-700">
          Bio (Optional)
        </label>
        <textarea
          id="bio"
          rows={4}
          placeholder="Tell us about yourself..."
          className="w-full rounded-md border border-gray-300 bg-gray-50 px-3 py-2 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400 resize-none"
          {...register("bio")}
        />
        {errors.bio && (
          <p className="text-xs text-red-600">{errors.bio.message}</p>
        )}
      </div>

      <button
        type="submit"
        disabled={pending}
        className="h-10 w-full rounded-md bg-pink-400 text-white text-sm font-semibold hover:bg-pink-500 transition-colors disabled:opacity-60"
      >
        {pending ? "Updating Profile..." : "üíæ Update Profile"}
      </button>
    </form>
  );
}
