'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import ProfileForm from '../_components/ProfileForm';

export default function ProfileClientPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchUser = async () => {
      try {
        // Get user data from cookies via server action
        const response = await fetch('/api/user/me');
        
        if (!response.ok) {
          throw new Error('Not authenticated');
        }
        
        const data = await response.json();
        setUser(data);
      } catch (error) {
        console.error('Failed to fetch user:', error);
        router.push('/login');
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [router]);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading profile...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return null;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-white to-purple-50 py-12 px-4">
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-800 mb-2">Your Profile</h1>
          <p className="text-gray-600">Manage your account information and preferences</p>
        </div>
        <ProfileForm
          userId={user.id || user._id}
          initialData={{
            name: user.name,
            email: user.email,
            bio: user.bio,
            phone: user.phone,
            image: user.image || user.profileImage,
          }}
        />
        <div className="mt-8 text-center">
          <p className="text-sm text-gray-500">
            Account created: {new Date(user.createdAt).toLocaleDateString()}
          </p>
          <p className="text-sm text-gray-500 mt-1">
            Role: <span className="font-medium text-pink-600">{user.role}</span>
          </p>
        </div>
      </div>
    </div>
  );
}
